* {
   box-sizing: border-box;
}

html,
body,
#root {
   width: 100%;
   height: 100%;
   margin: 0;
   padding: 0;
}

body {
   background: #333333;
   font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, helvetica neue, helvetica,
      ubuntu, roboto, noto, segoe ui, arial, sans-serif;
}
